export default class ArrayX extends Array {
  /**
   * 数组元素分组
   * @param {any} arr - 数组
   * @param {any} len - 每组的数量
   * @return {Array} 分组后的数组
   */
  static chunk(arr: any, len: any) {
    var chunks = [],
      i = 0,
      n = arr.length;

    while (i < n) {
      chunks.push(arr.slice(i, (i += len)));
    }

    return chunks;
  }
}
