import React from "react";
import LayoutContainer from "../Layouts/LayoutContainer";
interface IState {
  title: string;
  description: string;
  created_at?: any;
  updated_at?: any;
}
interface IProps {
  pageId: string;
}
export default class Page extends React.Component<IProps, IState> {
  state: IState = {
    title: "",
    description: ""
  };
  props: any;

  componentDidMount() {
    this.setState({
      title: "神秘博士",
      description: "英国科幻悬疑电视剧"
    });
  }

  render() {
    return (
      <React.Fragment>
        <LayoutContainer pageId={this.props.pageId} />
      </React.Fragment>
    );
  }
}
