import React, { Component, Suspense } from "react";
import ArrayX from "../Classes/ArrayX";
interface IState {
  components: any;
}
interface IProps {
  components: any;
}
export default class ComponentContainer extends Component<IProps, IState> {
  state: IState = {
    components: []
  };

  /**
   * 组件延时加载方案
   * 用ArrayChunk分批将组件发送到接口，每次传递4个组件到接口获取这4个组件的数据，数据获取完成后获取另外4个组件数据并且加载
   */
  componentDidMount() {
    let components: any = [];
    ArrayX.chunk(this.props.components, 4).forEach(componentsChunk => {
      componentsChunk.forEach((componentName: any) => {
        const LazyComponent = React.lazy(() =>
          import(`./${componentName}/${componentName}`)
        );
        components.push(LazyComponent);
      });
      this.setState({
        components: components
      });
    });
  }

  render() {
    let componentList = this.state.components.map(
      (LazyComponent: any, index: any) => {
        return (
          <React.Fragment key={index}>
            <Suspense key={index} fallback={<div>Loading...</div>}>
              <LazyComponent key={index} />
            </Suspense>
          </React.Fragment>
        );
      }
    );
    return <React.Fragment>{componentList}</React.Fragment>;
  }
}
