import React, { Suspense } from "react";
import ComponentContainer from "../Components/ComponentContainer";
interface IState {
  template: string;
  templateComponent?: any;
  rows: any;
}
interface IProps {
  pageId: number;
}
export default class LayoutContainer extends React.Component<IProps, IState> {
  state: IState = {
    template: "Default",
    templateComponent: null,
    rows: []
  };

  componentDidMount() {
    //加载页面所有的布局
    let rows: any = [
      [
        {
          col: 12,
          class: "header",
          components: ["MultiSelector"]
        }
      ],
      [
        {
          col: 8,
          class: "container",
          components: ["Editor", "Example", "Example", "Example", "Example"]
        }
      ]
    ];

    const LazyComponent = React.lazy(() =>
      import(`../Templates/${this.state.template}/${this.state.template}`)
    );
    this.setState({
      rows: rows,
      templateComponent: LazyComponent
    });
  }

  render() {
    let rows: any = this.state.rows.map((row: any, index: number) => {
      let cols: any = row.map((col: any, colIndex: number) => {
        return (
          <div className={`col-${col.col}`} key={colIndex}>
            <ComponentContainer components={col.components} />
          </div>
        );
      });
      return (
        <div className="row" key={index}>
          {cols}
        </div>
      );
    });

    let TemplateComponent = this.state.templateComponent;
    if (!TemplateComponent) {
      return <div>加载模板中</div>;
    }
    return (
      <React.Fragment>
        <Suspense fallback={<div>Loading...</div>}>
          <TemplateComponent>{rows}</TemplateComponent>
        </Suspense>
      </React.Fragment>
    );
  }
}
